.square-ratio {
  position: relative;
  overflow: hidden;
}

.square-ratio:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 100%;
}

.square-ratio>* {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.square-ratio>img {
  height: 100%;
  object-fit: cover;
}

.video-ratio {
  position: relative;
  overflow: hidden;
}

.video-ratio:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 5.625%;
}

.video-ratio>* {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.video-ratio>img {
  height: 100%;
  object-fit: cover;
}

.portrait-ratio {
  position: relative;
  overflow: hidden;
}

.portrait-ratio:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 177.77778%;
}

.portrait-ratio>* {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.portrait-ratio>img {
  height: 100%;
  object-fit: cover;
}

.main-container {
  display: grid;
  grid-template-columns: 20px 1fr 20px;
}

@media (max-width: 1024px) {
  .main-container {
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 460px) minmax(20px, 1fr);
  }
}

@media (max-width: 1024px) {
  .main-container {
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 460px) minmax(20px, 1fr);
  }
}

@media (min-width: 720px) {
  .main-container {
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 720px) minmax(20px, 1fr);
  }
}

@media (min-width: 1064px) {
  .main-container {
    grid-template-columns: minmax(20px, 1fr) minmax(auto, 1024px) minmax(20px, 1fr);
