html {
  line-height: 1.5em;
  text-align: center;
  font-family: clean;
  scroll-behavior: smooth;
}

body {
  margin: 0;
}

img {
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: clean;
  line-height: 1.2em;
  margin: 0 0 1.25rem;
}

h1 {
  font-size: 4.375rem;
}

p {
  font-family: clean;
}

.navbar-btn {
  background: none;
  border: none;
  font-family: clean;
  cursor: pointer;
  font-size: 1.375rem;
  font-weight: 600;
  outline: none;
  transition: all ease-out 0.7s;
  outline: none;
  margin: auto 0;
}

.resume-btn {
  font-size: 1.5rem;
  text-decoration: none;
  font-family: clean;
  font-weight: 600;
  color: #fff;
  padding: 5px;
}

.main-page-btn {
  font-family: clean;
  font-weight: bold;
  background: none;
  border: none;
  z-index: 9999;
  width: auto;
  font-size: 1.375rem;
  outline: none;
  cursor: pointer;
}

.toggle-btn {
  width: 100%;
  padding: .625rem;
  position: relative;
  z-index: 9999;
  display: flex;
  align-items: center;
  justify-content: center;
}

.toggle-btn .btn {
  position: relative;
  height: 40px;
  width: 100px;
  overflow: hidden;
  z-index: -5;
  border-radius: 20px;
  display: flex;
  align-items: center;
}

@media (max-width: 600px) {
  .toggle-btn .btn {
    position: absolute;
    bottom: -150%;
  }
}

.toggle-btn .btn.light {
  background-color: rgba(99,13,156,0.2);
}

.toggle-btn .btn.dark {
  background-color: rgba(99,13,156,0.2);
}

.toggle-btn .btn .inside-circle {
  border-radius: 50%;
  border: #fff solid 4px;
  transition: 0.4s;
  width: 35px;
  height: 85%;
  background-color: rgba(99,13,156,0.2);
}

.toggle-btn .btn .inside-circle.right-side {
  transform: translateX(58px);
  border: #aa7ed4 solid 4px;
}

.toggle-btn .clean-option,
.toggle-btn .space-option {
  width: 35%;
}

.toggle-btn .clean-option h4,
.toggle-btn .space-option h4 {
  margin: 0;
  font-size: 1.375rem;
}

.toggle-btn .clean-option .space,
.toggle-btn .space-option .space {
  color: #630d9c;
}

.toggle-btn span {
  position: absolute;
  margin: auto 0;
  cursor: pointer;
}

.toggle-btn .light {
  color: #fff;
}

.toggle-btn .dark {
  color: #1f1122;
}

.navbar {
  display: none;
  width: 100%;
  height: 90px;
  justify-content: space-between;
  align-items: center;
  padding: 0 40px;
  position: fixed;
  top: 0;
  z-index: 9999;
}

.navbar .logo {
  width: 50px;
  margin: 0;
}

.navbar .navbar-content-box {
  width: 600px;
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1024px) {
  .navbar {
    overflow-x: hidden;
    background: none !important;
    border: none !important;
    direction: rtl;
    display: flex;
    position: absolute;
  }

  .navbar img {
    display: none;
  }

  .navbar .navbar-content-box {
    flex-direction: column;
    justify-content: space-around;
    position: fixed;
    height: 100vh;
    top: 0;
    right: 0;
    width: 40%;
    min-width: 200px;
    padding-top: 80px;
    direction: ltr;
    transition: all ease-in 0.4s;
    background-color: rgba(99,13,156,0.85);
    color: #fff;
  }

  .navbar .navbar-content-box a {
    margin: 0;
  }

  .navbar .navbar-content-box .resume-btn {
    width: 70%;
    max-width: 150px;
    margin: 0 auto;
  }

  .navbar .close {
    transform: translateX(100%);
  }

  .navbar .hidden {
    display: none;
  }

  .navbar .hamburger {
    position: fixed;
    top: 5%;
    width: 35px;
    height: 35px;
    z-index: 9999;
  }

  .navbar .hamburger div {
    width: 100%;
    height: 5px;
    margin: 6px 0;
    transition: 0.4s;
    border-radius: 2px;
  }

  .navbar .hamburger .bar1,
  .navbar .hamburger .bar2,
  .navbar .hamburger .bar3 {
    background-color: #630d9c;
  }

  .navbar .change .bar1 {
    transform: rotate(-45deg) translate(-10px, 7px);
    background-color: #fff;
  }

  .navbar .change .bar2 {
    opacity: 0;
  }

  .navbar .change .bar3 {
    transform: rotate(45deg) translate(-8px, -6px);
    background-color: #fff;
  }
}

.landing {
  position: absolute;
  left: 100vw;
  top: 100vh;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: 15;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 0px 0px;
  gap: 0px 0px;
  grid-template-areas: 'options top .' 'left content right' '. bottom .';
}

.landing .left {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-area: left;
}

.landing .left svg {
  transform: rotate(90deg);
}

.landing .left:hover svg {
  -webkit-animation: left 2s ease-in-out infinite;
          animation: left 2s ease-in-out infinite;
}

.landing .right {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-area: right;
}

.landing .right svg {
  transform: rotate(-90deg);
}

.landing .right:hover svg {
  -webkit-animation: right 2s ease-in-out infinite;
          animation: right 2s ease-in-out infinite;
}

.landing .top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-area: top;
  z-index: 5;
}

.landing .top svg {
  transform: rotate(180deg) translateY(20px);
  margin: 0 auto;
}

.landing .top button {
  margin: 0 auto;
}

.landing .top:hover svg {
  -webkit-animation: top 2s ease-in-out infinite;
          animation: top 2s ease-in-out infinite;
}

.landing .bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  grid-area: bottom;
  z-index: 15;
}

.landing .bottom svg {
  transform: translateY(20px);
  margin: 0 auto;
}

.landing .bottom button {
  margin: 0 auto;
}

.landing .bottom:hover svg {
  -webkit-animation: bottom 2s ease-in-out infinite;
          animation: bottom 2s ease-in-out infinite;
}

.landing .content {
  z-index: 9999;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-area: content;
  font-family: imb-reg;
}

.landing .content .row {
  display: flex;
}

.landing .content .row p {
  padding: 0 4px;
}

.landing .content .name {
  font-family: clean;
  font-size: 1.5rem;
  padding: 10px;
}

.landing .content .sentence {
  font-family: clean;
  font-weight: 600;
  font-size: 1.5rem;
  margin-top: 10px;
}

.landing .content span {
  width: 100%;
  z-index: 15;
  color: #630d9c;
  font-size: 1.625rem;
  margin: 0 3px;
}

.landing .content h2 {
  font-size: 1.625rem;
}

.landing .animations {
  width: 100%;
  height: 100%;
  position: absolute;
}

.landing .animations .stars {
  width: 100%;
  position: absolute;
  top: 0;
}

.landing .animations .stars .star {
  position: absolute;
  width: 100%;
  top: 0;
}

.landing .animations .stars .group {
  position: absolute;
  width: 100%;
  top: 0;
}

.landing .animations .stars #star {
  fill: #fff;
}

@media (max-width: 1024px) {
  .landing {
    position: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
  }

  .landing .content {
    width: 80%;
    height: 40%;
  }

  .landing .left,
  .landing .top,
  .landing .right,
  .landing .bottom {
    display: none;
  }
}

@media (max-width: 475px) {
  .landing .content .name {
    font-size: 1.125rem;
  }

  .landing .content .name span {
    font-size: 1.375rem;
  }

  .landing .content .sentence {
    font-size: 1.125rem;
  }

  .landing .content span .toggle-btn div h4 {
    font-size: 1rem;
  }
}

@-webkit-keyframes left {
  0% {
    transform: rotate(90deg) translateY(0px);
  }

  50% {
    transform: rotate(90deg) translateY(30px);
  }

  100% {
    transform: rotate(90deg) translateY(0px);
  }
}

@keyframes left {
  0% {
    transform: rotate(90deg) translateY(0px);
  }

  50% {
    transform: rotate(90deg) translateY(30px);
  }

  100% {
    transform: rotate(90deg) translateY(0px);
  }
}

@-webkit-keyframes right {
  0% {
    transform: rotate(-90deg) translateY(0px);
  }

  50% {
    transform: rotate(-90deg) translateY(30px);
  }

  100% {
    transform: rotate(-90deg) translateY(0px);
  }
}

@keyframes right {
  0% {
    transform: rotate(-90deg) translateY(0px);
  }

  50% {
    transform: rotate(-90deg) translateY(30px);
  }

  100% {
    transform: rotate(-90deg) translateY(0px);
  }
}

@-webkit-keyframes top {
  0% {
    transform: rotate(180deg) translateY(20px);
  }

  50% {
    transform: rotate(180deg) translateY(50px);
  }

  100% {
    transform: rotate(180deg) translateY(20px);
  }
}

@keyframes top {
  0% {
    transform: rotate(180deg) translateY(20px);
  }

  50% {
    transform: rotate(180deg) translateY(50px);
  }

  100% {
    transform: rotate(180deg) translateY(20px);
  }
}

@-webkit-keyframes bottom {
  0% {
    transform: translateY(20px);
  }

  50% {
    transform: translateY(50px);
  }

  100% {
    transform: translateY(20px);
  }
}

@keyframes bottom {
  0% {
    transform: translateY(20px);
  }

  50% {
    transform: translateY(50px);
  }

  100% {
    transform: translateY(20px);
  }
}

.social {
