.blog h2 {
  font-size: 20px;
  color: #9013e3;
}

.blog .post {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 25%;
  height: 50%;
  margin: auto 15px;
  text-decoration: none;
  opacity: 0.6;
}

.blog .post img {
  width: 80%;
  box-shadow: 0 1px 4px rgba(0,0,0,0.3),0 0 40px rgba(0,0,0,0.1);
  margin-top: 10px;
}

.blog .post h3 {
  font-family: Lucida Sans Unicode;
  font-size: 1rem;
  margin-top: 15px;
  height: 100%;
  color: #1f1122;
}

.blog .post.center {
  width: 40%;
  height: 80%;
  opacity: 1;
}

.blog .post.center h3 {
  font-size: 1.375rem;
}

@media (max-width: 720px) {
  .blog .post {
    width: 80% !important;
    margin: 20px auto;
  }

  .blog .post img {
    margin: 20px auto;
  }

  .blog .post h3 {
    font-size: 1.125rem !important;
  }
}

@media (max-width: 420px) {
  .blog .post img {
    width: 100% !important;
  }
}

.blog .to-site-link {
  position: absolute;
  bottom: 10vh;
  font-size: 1.375rem;
  z-index: 10;
  font-weight: 600;
  width: 100%;
  text-align: center;
  margin: 0 auto;
}

.blog .to-site-link a {
  text-decoration: none;
  color: #1f1122;
}

.blog footer {
  position: fixed;
  bottom: 0;
  left: 0;
  padding: 1vh;
  width: 100%;
  font-size: .875rem;
  font-weight: 500;
  color: #91979e;
  border-top: solid 1px #91979e7c;
}

.blog-post-page {
  width: 100%;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.blog-post-page .blog-header {
  width: 100%;
  height: 20vh;
  background-color: #1f1122;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog-post-page .blog-header svg {
  width: 45%;
  height: 90%;
}

.blog-post-page h1 {
  font-size: 2.625rem;
  color: #1f1122;
  margin: 20px 0;
}

.blog-post-page .post-page-content {
  width: 80%;
  margin: 20px auto;
}

.blog-post-page .post-page-content h2 {
  text-align: start;
  font-size: 2rem;
}

.blog-post-page .post-page-content p {
  text-align: start;
  font-size: 1.25rem;
  font-weight: 600;
  line-height: 30px;
}

.blog-post-page .post-page-content h3 {
  text-align: start;
  font-size: 1.625rem;
}

.blog-post-page .post-page-content .file-source {
  background-color: rgba(99,13,156,0.5);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.blog-post-page .post-page-content .post-link {
  text-align: start;
  width: 100%;
  font-size: 1.25rem;
  font-weight: 600;
  text-decoration: none;
  color: rgba(99,13,156,0.9);
}

.blog-post-page .post-page-content .code-block {
  background-color: rgba(165,165,165,0.281);
  margin: 0;
  padding-top: 10px;
}

.blog-post-page footer {
  padding: 1vh;
  width: 100%;
  font-size: .875rem;
  font-weight: 500;
  color: #91979e;
  border-top: solid 1px #91979e7c;
}

@media (max-width: 720px) {
  .blog-post-page h1 {
    font-size: 2rem;
  }

  .blog-post-page .post-page-content h2 {
    font-size: 1.625rem;
  }

  .blog-post-page .post-page-content h3 {
    font-size: 1.25rem;
  }

  .blog-post-page .post-page-content p {
    font-size: 1rem;
  }

  .blog-post-page .post-page-content .post-link {
    font-size: 1rem;
  }
}


